.sectionStyle {
    background: 
    var(--blue-main);
  color: var(--text-main-light);
  }

  

.name, .position, .title {
    color: var(--text-on-blue-main); /* білий — для виділення */
  }
  
  .degree, .org {
    color: var(--text-on-blue-muted); /* приглушено-сірий або бежевий */
  }

.heroStyle {

    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 40px;

    margin: 0 auto;
    padding: 20px;
    padding-bottom: 28px;
    padding-top: 28px;

    max-width: 100%;
}

.title {
    width: 100%;
    font-size: 14px;
    line-height: 1.28;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 16px;
}


.imageWrapper{
    width: 100%;
    max-width: 180px;
}

.imageWrapper img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 14px;
    box-shadow:
      0 8px 16px rgba(0, 0, 0, 0.15),
      0 16px 40px rgba(0, 0, 0, 0.25);
}

.info {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 12px;
    margin-left: auto;
    margin-right: auto;
}

.infoShort {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.additionalInf{
    display: flex;
    flex-direction: column;
}

.name {
    font-size: 1.4rem;
}

.position{
    font-weight: bold;
    font-size: 1.1rem;
}

.org{
    font-size: 1rem;
}

.degree{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 4px;

    margin-left: auto;
    margin-right: auto;
    margin-bottom: 8px;

    font-size: 1rem;
}

@media screen and (min-width: 320px) {

.imageWrapper{
    max-width: 240px;
}

.name{
    font-size: 1.6rem;
}

} 

@media screen and (min-width: 768px) {

    .heroStyle{
        align-items: flex-start;
        text-align: left;
        gap: 32px;

        padding: 32px;
    }

    .title {
        gap: 16px;
        font-size: 16px;
    }

    .info{
        flex-direction: row;
        gap: 28px
    }

    .infoShort {
        gap: 10px;
    }

    .imageWrapper{
        max-width: 280px;
    }

    .additionalInf{
        flex-direction: row;
        justify-content: center;
        align-items: center;
        gap: 200px;
    }

    .name{
        font-size: 2.2rem;
        line-height: 2.2rem;
    }

    .position{
        font-size: 1.4rem;
    }

    .org{
        font-size: 1.2rem;
    }
    

    .degree{
        text-align: center;
        font-size: 1.2rem;
    }
} 

@media screen and (min-width: 1440px) {

    .info{
        gap: 72px;
    }

    .heroStyle{
        gap: 40px;
        padding: 48px;
        align-items: flex-start;
    }

    .infoShort {
        gap: 28px;
    }

    .title{
        font-size: 20px;
        gap: 12px;
    }

    .name{
        font-size: 2.5rem;
    }

    .additionalInf{
        gap: 680px;
    }

    .position{
        font-size: 1.7rem;
    }


}


@media screen and (max-width: 767px) {
    .heroStyle {
      padding: 24px 16px;
      gap: 32px;
    }
  
    .info {
      flex-direction: column;
      align-items: center;
      gap: 16px;
      text-align: center;
    }
  
    .imageWrapper {
      max-width: 200px;
    }
  
    .name {
      font-size: 1.4rem;
      line-height: 1.4;
    }
  
    .position {
      font-size: 1rem;
      font-weight: 600;
    }
  
    .org, .degree {
      font-size: 0.9rem;
    }
  
    .additionalInf {
      flex-direction: column;
      align-items: center;
      gap: 24px;
      text-align: center;
    }
  
    .infoShort {
      gap: 8px;
    }
  
    .degree {
      gap: 4px;
      margin-bottom: 12px;
    }
  }